.trigger {
  padding: 0 24px;
  font-size: 18px;
  line-height: 64px;
  cursor: pointer;
  transition: color 0.3s;
}

.trigger:hover {
  color: #1890ff;
}

.logo {
  height: 32px;
  margin: 16px;
  background: rgba(255, 255, 255, 0.3);
  text-align: center;
  color: #fff;
  font-size: 20px;
  font-family: cursive;
  font-weight: lighter;
}

.site-layout .site-layout-background {
  background: #fff;
}

.ant-layout-has-sider {
  height: 100vh;
}

.error-screen {
  border: 1px solid red;
  padding: 12px;
  background: lightyellow;
}